.main-header {
  height: 80vh;

  .headlines {
    position: relative;
    top: 70px;
  }

  .showRoom-title {
    font-family: Lobster;
    font-size: 120px;
    color: $clr1;
  }

  .marketplace-title {
    font-family: Lobster;
    font-size: 40px;
    position: relative;
    bottom: 80px;
    left: 160px;
    color: $clr1;
  }

  .menu-icons {
    > * {
      padding-left: 15px;
    }
    width: rem(80px);
    height: rem(80px);
  }
  .cover-photo {
    object-fit: cover;
    object-position: top;
    height: 80vh;
    // filter: brightness(40%);
  }

  .nav-text {
    > * {
      padding-right: 15px;
    }

    color: $clr4;
    font-size: rem(16px);
  }

  .nav-icon {
    padding: 10px;
    position: fixed;
    height: 60px;
    z-index: 4;
    padding: 10px;
    width: 100%;
  }

  .up {
    background-color: $clr1;
    transition: background-color 0.4s ease-out;
  }

  .bell-icon {
    width: 30px;
    height: 30px;
  }
  .notification-seller-badge {
    right: 115px;
    position: absolute;
    top: 14px;
    color: black;
  }
}

.header-text-search {
  position: absolute;
  bottom: 38vh;
  right: 32vw;
  color: white;
}

.search {
  z-index: 3;
  input {
    width: 600px;
    height: 50px;
    padding-left: 10px;
    background-color: $clr4;
    border: 0px;
  }
  input::placeholder {
    font-size: rem(18px);
    font-style: italic;
  }
  button {
    font-size: rem(18px);
    margin-top: 20px;
    background-color: $clr1;
    position: relative;
    left: 240px;
    bottom: 65px;
    border: 0px;
    width: 100px;
    height: 40px;
    color: $clr4;
  }
}

.text-flicker-in-glow {
  animation: text-flicker-in-glow 4s linear both;
}

