.main-section {
  height: 450px;
  position: relative;

  img {
    object-fit: cover;
    object-position: center;
  }
  h1 {
    // position: absolute;
    // bottom: 20px;
    // right: 20px;
  }
}

.home-categories {
  div {
    background-color: lightcyan;
  }
  display: grid;
  min-height: 100vh;
  grid-template-columns: auto;
  grid-template-rows: 50px 50px;
  grid-template-areas:
    "b b c c c"
    "b b c c c"
    "b b c c c"
    "d d c c c"
    "d d c c c"
    "d d c c c"
    "d d c c c"
    "e e e e e"
    "e e e e e";
}

.category-c {
  grid-area: c;
  // background-image: url("../assets/imgs/1111.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  &:hover {
    background-blend-mode: luminosity;

  }
}

.category-a {
  grid-area: a;
  background-size: 100% 100%;
}

.category-b {
  // background-image: url("https://img.rawpixel.com/s3fs-private/rawpixel_images/website_content/383-jir9305-jir_1.jpg?auto=format&bg=transparent&con=3&cs=srgb&dpr=1&fm=jpg&ixlib=php-3.1.0&mark=rawpixel-watermark.png&markalpha=90&markpad=13&markscale=10&markx=25&q=75&usm=15&vib=3&w=1200&s=794b8342a8c4ad28e3f4a87483d2bfbb");
  background-position: center;
  background-size: cover;
  grid-area: b;
  &:hover {
    background-blend-mode: luminosity;
  }
}

.category-d {
  // background-image: url("https://img.rawpixel.com/s3fs-private/rawpixel_images/website_content/393-mckinsey-21a3921-jir_1.jpg?auto=format&bg=transparent&con=3&cs=srgb&dpr=1&fm=jpg&ixlib=php-3.1.0&mark=rawpixel-watermark.png&markalpha=90&markpad=13&markscale=10&markx=25&q=75&usm=15&vib=3&w=1200&s=8d214bc588282e34255fbd1e08aedb78");
  background-size: 100% 100%;
  grid-area: d;

  &:hover {
    background-blend-mode: luminosity;

  }
}

.category-e {
  // background-image: url("https://img.rawpixel.com/s3fs-private/rawpixel_images/website_content/383-jir9259-jir-l_1.jpg?auto=format&bg=transparent&con=3&cs=srgb&dpr=1&fm=jpg&ixlib=php-3.1.0&mark=rawpixel-watermark.png&markalpha=90&markpad=13&markscale=10&markx=25&q=75&usm=15&vib=3&w=1200&s=d4f6458396ac8d5e2c9c63ffd7c47e16");
  background-size: 100% auto;
  background-repeat: no-repeat;
  grid-area: e;
  background-position: center;
  &:hover {
    background-blend-mode: luminosity;
    
  }
}

